import {createSlice} from "@reduxjs/toolkit";

const initialState = {
    searchValue: "",
    categoryId: 0,
    currentPage: 1,
    sort: {name: "популярности", sortProperty: "rating"}
};

const filterSlice = createSlice({
    name: "filters",
    initialState,
    reducers: {
        setCategoryId(state, action) {
            state.categoryId = action.payload;//payload представляет собой данные, которые передаются вместе с действием (action) при вызове функции setCategoryId.
        },
        setSearchValue(state, action) {
            state.searchValue = action.payload;
        },
        setSort(state, action) {
            state.sort = action.payload;
        },
        setCurrentPage(state, action) {
            state.currentPage = action.payload;
        },
        setFilters(state, action) {
            state.currentPage = Number(action.payload.categoryId);
            state.sort = action.payload.sort;
            state.categoryId = Number(action.payload.categoryId);
        },
    }
});

export const {setSearchValue,setCategoryId, setSort, setCurrentPage, setFilters} = filterSlice.actions;

export default filterSlice.reducer;